.switch-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}
    
.switch-wrapper > div {
    position: absolute;
    width: 100%;
}

.custom-ratio-content {
    position: absolute !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.press-btn:hover {
    transform: scale(1.1);
    /* transform-origin: center center; */
}
/* .press-btn:active {
} */

.mdc-text-field__input:focus {
    outline: 1px solid white !important;
}

.bmi-kb-btn {
    font-size: 3vmin; 
    color: black; 
    position: absolute;
    background-color: white;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.bmi-kb-btn:active {
    color: lightgray;
}

.bmi-kb-inner-text {
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%); 
    position: absolute;
}

.page-root-div {
    background: black;
    width: 100%;
    height: 100%;
    position: absolute;
}

.page-root-with-cursor-div {
    background: black;
    width: 100%;
    height: 100%;
    position: absolute;
    cursor: url(assets/images/shopping_scanner_cursor.png) 19 18, pointer;
}

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-20%);
	}
	100% {
		transform: translatey(0px);
	}
}

.pointer-hand {
    animation: float 1s ease-in-out infinite;
}